import {
  CButton,
  CCol,
  CContainer,
  CForm,
  CFormInput,
  CFormLabel,
  CFormSelect,
  CFormTextarea,
  CImage,
  CRow,
  CSpinner,
} from "@coreui/react";
import axios from "axios";
import React, { useCallback, useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import { toast } from "react-toastify";
import { api, headers } from "src/views/api";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import CIcon from "@coreui/icons-react";
import { cilTrash } from "@coreui/icons";

const EditProduct = () => {
  const [fileData, setFileData] = useState([]);
  const [file, setFile] = useState("");

  const [loading, setLoading] = useState(true);
  const [images, setImages] = useState([]);
  const [product, setProduct] = useState({});
  const [filters, setFilters] = useState([]);
  const [selectedFilters, setSelectedFilters] = useState([]);
  const [selectedSubFilters, setSelectedSubFilters] = useState([]);
  const [categories, setCategories] = useState([]);

  const [stop, setStop] = useState(false);

  const params = useParams();
  const { push } = useHistory();

  useEffect(() => {
    const fetchData = async () => {
      const product = await axios.get(`${api}/api/product/${params?.id}`);
      const categories = await axios.get(`${api}/api/categories`);
      const filters = await axios.get(
        `${api}/api/filter/category/${product?.data?.categories}`
      );

      setImages(product?.data?.images);
      setProduct(product?.data);
      setCategories(categories?.data?.categories);
      setFilters(filters?.data);
      setSelectedFilters(product?.data?.filters);
      setLoading(false);
    };

    fetchData();
  }, []);

  const renderCategories = (categories) => {
    let myCategories = [];

    for (let category of categories) {
      myCategories.push(
        <ul style={{ padding: 8 }}>
          <li style={{ listStyleType: "none" }}>
            <input
              style={{
                marginLeft: 8,
                position: "relative",
                top: 2,
                cursor: "pointer",
              }}
              type="checkbox"
              value={category._id}
              onChange={async (e) => {
                setSelectedFilterChild([]);
                const categoryId = category._id;
                await axios
                  .get(`${api}/api/filter/category/${categoryId}`, { headers })
                  .then((res) => {
                    setFilters(res.data);
                  })
                  .catch((err) => {
                    console.log(err);
                  });

                setProduct({
                  ...product,
                  categories: e.target.value,
                });
              }}
              checked={product?.categories === category._id}
            />
            {category?.name}
            {category?.children?.length > 0 && (
              <ul style={{ padding: 0, paddingRight: 10 }}>
                {renderCategories(category.children)}
              </ul>
            )}
          </li>
        </ul>
      );
    }

    return myCategories;
  };

  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    rtl: true,
  };

  const persianNumbers = [
      /۰/g,
      /۱/g,
      /۲/g,
      /۳/g,
      /۴/g,
      /۵/g,
      /۶/g,
      /۷/g,
      /۸/g,
      /۹/g,
    ],
    arabicNumbers = [
      /٠/g,
      /١/g,
      /٢/g,
      /٣/g,
      /٤/g,
      /٥/g,
      /٦/g,
      /٧/g,
      /٨/g,
      /٩/g,
    ],
    fixNumbers = function (str) {
      if (typeof str === "string") {
        for (var i = 0; i < 10; i++) {
          str = str.replace(persianNumbers[i], i).replace(arabicNumbers[i], i);
        }
      }
      return str;
    };

  const handleUpload = useCallback(
    async (id) => {
      if (fileData?.length && id) {
        try {
          for (let i = 0; i < fileData.length; i++) {
            const bodyFormData = new FormData();
            bodyFormData.append("image", fileData[i]);

            await axios({
              method: "post",
              url: `${api}/api/product/upload/${id}`,
              data: bodyFormData,
              headers: {
                "Content-Type": "multipart/form-data",
                "x-auth-token": localStorage.token,
              },
            });
          }
        } catch (error) {
          console.log(error?.response);
        }
      }
    },
    [fileData]
  );

  const handleSubmit = async (e) => {
    e.preventDefault();
    product.price = fixNumbers(product.price);
    product.discount = fixNumbers(product.discount);

    const response = await axios.put(
      `${api}/api/product/${params?.id}`,
      product,
      {
        headers,
      }
    );

    if (response?.status === 200) {
      handleUpload(params?.id);
      toast.success("محصول با موفقیت ویرایش شد");
      push("/products");
    }
  };

  const handleChange = (e) =>
    setProduct({ ...product, [e.target.name]: e.target.value });

  useEffect(() => {
    if (selectedFilters?.length && !stop) {
      filters?.forEach(async (element, i) => {
        console.log(element);
        const result = await axios.get(
          `${api}/api/filter/category/value/${element?._id}/${selectedFilters?.[i]?.value}`,
          { headers }
        );
        if (result?.data?.length) {
          console.log({ ...result?.data?.[i], parent: element?._id });
          setSelectedSubFilters([
            ...selectedSubFilters,
            { ...result?.data?.[i], parent: element?._id },
          ]);
        }
        setStop(true);
      });
    }
  }, [selectedFilters]);

  console.log({
    product,
    selectedSubFilters,
  });

  return (
    <CContainer>
      <CRow>
        <CCol md={12}>
          <div class="d-flex flex-row docs-highlight mb-3 justify-content-center">
            <div class="p-2 docs-highlight">
              {loading ? (
                <CSpinner />
              ) : (
                <CForm onSubmit={handleSubmit}>
                  <div className="mb-3">
                    <CFormLabel htmlFor="name">
                      <p class="fs-3">نام محصول</p>
                    </CFormLabel>
                    <CFormInput
                      type="text"
                      id="name"
                      placeholder="نام محصول را اینجا وارد کنید"
                      name="name"
                      onChange={handleChange}
                      value={product.name}
                    />
                  </div>
                  <div className="mb-3">
                    <CFormLabel htmlFor="name">
                      <p class="fs-3">دسته بندی محصول</p>
                    </CFormLabel>
                    <div className="d-flex justify-content-between flex-wrap">
                      {categories?.length && renderCategories(categories)}
                    </div>
                  </div>
                  <div className="mb-3">
                    <CFormLabel htmlFor="name">
                      <p class="fs-3">فیلتر</p>
                    </CFormLabel>
                    <div className="d-flex flex-wrap">
                      {filters?.map((el, i) => (
                        <div style={{ width: 150, marginLeft: 16 }}>
                          <div>
                            <h5>{el.name}</h5>
                            <p>{el.children.title}</p>
                          </div>
                          <div>
                            <select
                              style={{ width: "100%", marginBottom: 8 }}
                              value={selectedFilters?.[i]?.value}
                              onChange={async (e) => {
                                let arr = [...selectedFilters];
                                arr[i] = {
                                  ...arr[i],
                                  value: e.target.value,
                                };
                                setSelectedFilters(arr);
                                setProduct({
                                  ...product,
                                  filters: arr,
                                });
                                const result = await axios.get(
                                  `${api}/api/filter/category/value/${el?._id}/${e?.target?.value}`,
                                  { headers }
                                );
                                if (result.data.length) {
                                  const data = result.data?.[0];
                                  data.parent = el?._id;
                                  setSelectedSubFilters([data]);
                                }
                              }}
                            >
                              {el.values?.map((value) => (
                                <option value={value?._id}>
                                  {value?.name}
                                </option>
                              ))}
                            </select>
                          </div>
                          {selectedSubFilters?.map((elm, i) => {
                            if (elm?.parent === el?._id) {
                              return (
                                <div style={{ width: 150, marginLeft: 16 }}>
                                  <select
                                    style={{ width: "100%" }}
                                    value={product?.subFilter}
                                    onChange={async (e) => {
                                      let arr = [...product.subFilter];
                                      let filter = arr.filter(
                                        (ell) => ell.parent !== el?._id
                                      );
                                      filter.push({
                                        parent: el?._id,
                                        value: e?.target?.value,
                                        title: el?.children?.title,
                                      });

                                      setProduct({
                                        ...product,
                                        subFilter: filter?.flat(),
                                      });
                                    }}
                                  >
                                    {elm.values?.map((value) => (
                                      <option value={value?._id}>
                                        {value?.name}
                                      </option>
                                    ))}
                                  </select>
                                </div>
                              );
                            }
                          })}
                        </div>
                      ))}
                    </div>
                  </div>
                  <div className="mb-3">
                    <CFormLabel htmlFor="name">
                      <p class="fs-3">عکس محصول</p>
                    </CFormLabel>
                    <div
                      style={{
                        width: 400,
                        height: 400,
                        marginBottom: 48,
                        overflow: "hidden",
                      }}
                    >
                      <Slider {...settings}>
                        {images?.length &&
                          images.map((el) => (
                            <div
                              style={{
                                position: "relative",
                                width: 400,
                                height: 400,
                              }}
                            >
                              <div>
                                <CIcon
                                  icon={cilTrash}
                                  style={{
                                    color: "red",
                                    cursor: "pointer",
                                    position: "absolute",
                                    top: 10,
                                    left: 10,
                                    borderRadius: "50%",
                                    border: "1px solid red",
                                    padding: 3,
                                    width: 25,
                                    height: 25,
                                  }}
                                  title="حذف عکس"
                                  onClick={() => {
                                    const filter = images.filter(
                                      (elem) => elem !== el
                                    );

                                    setImages(filter);
                                    setProduct({
                                      ...product,
                                      images: product?.images?.filter(
                                        (image) => image !== el.split("/").pop()
                                      ),
                                    });
                                  }}
                                />
                              </div>
                              <img
                                style={{
                                  width: "100%",
                                  height: "100%",
                                  objectFit: "cover",
                                }}
                                src={`${api}/uploads/admin/product/${product?._id}/${el}`}
                                alt="slider"
                              />
                            </div>
                          ))}
                      </Slider>
                    </div>
                    <div className="form-file">
                      <input
                        type="file"
                        className="form-file-input"
                        id="customFile"
                        style={{ display: "none" }}
                        onChange={(e) => {
                          setFileData([...fileData, e.target.files[0]]);
                          setFile(URL.createObjectURL(e.target.files[0]));
                          setImages([
                            ...images,
                            URL.createObjectURL(e.target.files[0]),
                          ]);
                        }}
                      />
                      <label className="form-file-label" htmlFor="customFile">
                        <span className=" m-3">عکس محصول را انتخاب کنید</span>
                        <span className="btn btn-primary">کلیک کنید</span>
                      </label>
                    </div>
                  </div>
                  <div className="mb-3">
                    <CFormLabel htmlFor="name">
                      <p class="fs-3">قیمت</p>
                    </CFormLabel>
                    <CFormInput
                      type="text"
                      id="price"
                      placeholder="قیمت محصول"
                      name="price"
                      onChange={handleChange}
                      value={product.price}
                    />
                  </div>
                  <div className="mb-3">
                    <CFormLabel htmlFor="name">
                      <p class="fs-3">توضیحات محصول</p>
                    </CFormLabel>
                    <CFormTextarea
                      id="productDescription"
                      rows="3"
                      name="description"
                      onChange={handleChange}
                      value={product.description}
                    ></CFormTextarea>
                  </div>

                  <div className="mb-3">
                    <CButton color="success" type="submit">
                      ویرایش
                    </CButton>
                  </div>
                </CForm>
              )}
            </div>
          </div>
        </CCol>
      </CRow>
    </CContainer>
  );
};

export default EditProduct;
